import React from 'react';

const Card = ({robot}) => {
    const {name, email, id} = robot
    return (
        <div className='tc bg-light-green dib br3 pa3 ma2 grow bw shadow-5'>
            <img alt='Jane Doe' src={`https://robohash.org/${id}?size=200x200`} />
            <div>
                <h2>{name}</h2>
                <p>{email}</p>
            </div>
        </div>
    )
}

export default Card


